@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;500&display=swap");
* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Noto Sans JP", sans-serif;
  font-weight: 400;
}
.container-fluid {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  width: 360px;
  padding: 0;
}

.drum-pad {
  font-size: 50px;
  width: 100px;
  height: 100px;
  margin: 10px;
  display: grid;
  place-items: center;
}
.btn:active {
  position: relative;
  opacity: 0.7;
  transform: scale(0.95);
}

.card-header {
  width: 360px;
}
.card-footer {
  padding: 10px;
}
.card-footer .btn {
  width: 50%;
}
.btn-danger {
  margin-right: 1%;
}
.btn-success {
  margin-left: 1%;
}

a,
a:hover {
  text-decoration: none;
  color: #fff;
}

@media only screen and (min-width: 250px) and (max-width: 425px) {
  .container {
    width: 280px;
  }

  .drum-pad {
    font-size: 20px;
    width: 80px;
    height: 80px;
    margin: 5px;
  }

  .card-header {
    width: 260px;
  }
}
